
import Spinner from 'vux/src/components/spinner/index.vue'

    export default{
        components:{
            Spinner
        },
        data(){
            return{
                loading:true,
                complete:false,
                detailData:{}
            }
        },
        created(){
            this.getDetail();
        },
        mounted(){
            console.log(this.$route.params)
        },
        methods:{
            getDetail:function(){
                let self=this;
                $.ajax({
                    url: 'https://api.douban.com/v2/movie/subject/'+self.$route.params.id,
                    dataType: 'jsonp',
                    type: 'get',
                    success: function (res) {
                        self.detailData=res;
                        // ,
                        self.detailData.img=res.images.large;
                        self.detailData.average=res.rating.average;
                        self.detailData.directors=res.directors;
                        self.complete=true;
                        self.loading=false;
                    }
                })
            }
        }
    }



